import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { InputGroupInline, InputType, REFERENCE, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMLF } from '../../../../../dependencies/empresaMLFDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { SIMPLES_NACIONAL_REL_ENUM_VALUES, SINTETICO } from '../../../../../models/enumerated/mlf/simplesNacionalRelEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, REPORT_EVENT, REPORT_TITLE, SimplesNacionalRV } from './simplesNacionalRV';

const DEFAULT_VALUES: SimplesNacionalRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  gerarCabecalhoPadraoAdministradora: true,
  tipoRelatorioSimplesNacional: SINTETICO.value,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, REFERENCE.value),
  exibirConfiguracaoDadosEmpresa: true,
};

const SimplesNacionalReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<SimplesNacionalRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  return (
    <WindowComponent
      title="Simples Nacional"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMLF(globalReducer.globalParameter)]}
      wikiId={1933}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      >
        <InputGroupInline
          id="tipoRelatorioSimplesNacional"
          label="Tipo de Relatório"
          isPrependLabel={true}
          placeholder="Informe o Tipo de Relatório"
          formProps={{ ...formProps, validation: { required: true } }}
          inputDropDownProps={{
            optionsProvider: SIMPLES_NACIONAL_REL_ENUM_VALUES,
            selectProps: { isClearable: false },
          }}
          type={InputType.DROP_DOWN}
        />
      </ReportForm>
    </WindowComponent>
  );
};

export default SimplesNacionalReportView;
